:root {
  /* Colors */
  --jet: hsl(0, 0%, 22%);
  --onyx: hsl(240, 1%, 17%);
  --eerie-black-1: hsl(240, 2%, 13%);
  --eerie-black-2: hsl(240, 2%, 12%);
  --smoky-black: hsl(0, 0%, 7%);
  --white-1: hsl(0, 0%, 100%);
  --white-2: hsl(0, 0%, 98%);
  /* --orange-yellow-crayola: hsl(45, 100%, 72%); */
  /* --vegas-gold: hsl(45, 54%, 58%); */
  --light-gray: hsl(0, 0%, 84%);
  --light-gray-70: hsla(0, 0%, 84%, 0.7);
  --bittersweet-shimmer: hsl(0, 43%, 51%);

  /* Typography */
  --ff-poppins: 'Poppins', sans-serif;
  --fs-1: 24px;
  --fs-2: 18px;
  --fs-3: 17px;
  --fs-4: 16px;
  --fs-5: 15px;
  --fs-6: 14px;
  --fs-7: 13px;
  --fs-8: 11px;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;

  /* Transitions */
  --transition-1: 0.25s ease;
  --transition-2: 0.5s ease-in-out;

   --bg-color:#1f242d;
  --second-bg-color:#323946;
  --text-color:#fff;
  --main-color:#0ef; 
}

html {
  font-family: var(--ff-poppins);
}

body {
  background: var(--smoky-black);
  color: var(--white-1);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Aside Block */
.asideBlock {
  max-width: 280px; /* Increased width */
  background: var(--eerie-black-2);
  border-radius: 20px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 1.0);
  backdrop-filter: blur(10px);
  /* border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--white-1);
  margin: auto; /* Center the block 
  height:200px; */
}

.separator {
  width: 100%;
  height: 1px;
  background: white;
  margin: 16px 0;
}

/* .asideBlockContent {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
} */

.asideBlockInfo {
  display: flex;
  flex-direction: column;
  
 
}

.contactList {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.contactItem {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: var(--fs-6);
  margin-bottom: 40px; /* Increased margin */
}

.contactItem p {
  margin: 0;
}

.contactDetail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contactDetail a {
  color: var(--white-1);
  text-decoration: none;
  transition: color var(--transition-1);
}

.contactDetail a:hover {
  color: var(--main-color); /* Highlight on hover */
}

.contactItemIcon {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  background: var(--transition-1);
}

.contactItemIcon:hover {
  background: rgba(255, 255, 255, 0.4);
}

.profilebox {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 20%;
  padding: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.profile {
  width: 100%;
  border-radius: 20px;
}

.name {
  font-size: var(--fs-1);
  font-weight: var(--fw-400);
  padding: 0;
}

.multiple-text {
  font-size: var(--fs-4);
  font-weight: var(--fw-400);
  height: 24px; /* Ensure a consistent height */
  display: flex;
  align-items: center; /* Center text vertically */
}

.social-media a{
  display: inline-flex;
  justify-content: center;
  align-items: center; 
  width: 3rem;
  height: 3rem; 
  background: transparent; 
  border: .2rem solid white;
  border-radius: 50%;
  font-size: 2rem;
  color: white;
   margin:  2rem 9px; 
 
  text-decoration: none
}



*{
    box-sizing: border-box;
  
}



.App {
    display: flex;
    gap: 2%;
    margin: 3% 10%;
    
    
}

.navBar {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    text-align: left;
    padding:2%;
    
    border-radius: 20px;
    
    
}

.profile {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}




.list {
    text-decoration: none;
}

/* Remove list decoration */
ul, ol {
    list-style-type: none;
    padding: 0; /* Remove default padding */
    margin: 0;  /* Remove default margin */
}

/* Additional styling for better structure and readability */
body {
    font-family: var(--ff-poppins);
    color: var(--white-1);
    margin: 0;
    padding: 0;
}

nav a {
    color: var(--light-gray-70);
