}

nav a:hover {
    color: var(--main-color);
}



p {
    font-size: var(--fs-4);
    line-height: 1.6;
}



/* Example of specific component styling */
.Portfolio, .About, .Resume, .Contact {
    padding: 20px;
    margin: 10px;
    background-color: var(--eerie-black-2);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 1.0);
}

.content {
    background: var(--eerie-black-2);
    border: 1px solid var(--jet);
    border-radius: 20px;
    padding: 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 1.0);
    width: 100%; /* Adjust the width as needed */
}



.TopSection {
    text-align: center;
    margin-bottom: 40px;
}

.TopSection h1 {
    font-size: var(--fs-1);
   
    font-weight: var(--fw-600);
}

.coloredSeparator {
    width: 50px;
    height: 4px;
    background-color: var(--main-color);
    margin: 10px auto;
}

.coloredSeparater{
  width: 50px;
  height: 4px;
