  border-radius: 2px;
  margin: 30px 0;
}

.PortfolioSection {
    display: flex;
    flex-direction: column;
    align-items: center;
}

nav {
    margin-bottom: 20px;
    
}


nav a {
    margin: 0 10px;
    text-decoration: none;
    font-size: var(--fs-4);
    font-weight: var(--fw-500);
    transition: color var(--transition-1);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}



.ProjectBox {
  display: grid;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 20px;
}

.projectbox {
  text-align: center;
  max-width: 200px;
}

.ProjectImage {
    width: 100%;
    height: 150px;
    overflow: hidden;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
}

.ProjectImage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition-1);
}

.ProjectImage img:hover {
    transform: scale(1.05);
}

.ProjectName {
    font-size: var(--fs-3);
    font-weight: var(--fw-600);
    
}

.ProjectType {
